import React from 'react';
import { Users, Calendar, ClipboardCheck, TrendingUp } from 'lucide-react';

const Stat = ({ title, value, icon: Icon }) => (
  <div className="p-4 shadow-sm card bg-base-100">
    <div className="flex items-center justify-between">
      <div>
        <p className="text-sm text-gray-500">{title}</p>
        <p className="text-2xl font-bold text-gray-900">{value}</p>
      </div>
      <div className="flex items-center justify-center w-10 h-10 text-white rounded-lg bg-emerald-500">
        <Icon className="w-5 h-5" />
      </div>
    </div>
  </div>
);

export default function Homepage({ setCurrentPage }) {
  return (
    <div>
      <div className="mb-6">
        <h1 className="text-2xl font-bold text-gray-900">Dashboard Overview</h1>
        <p className="text-sm text-gray-500">Monitoring survei 360Â° seluruh property</p>
      </div>

      <div className="space-y-8">
        <div className="grid grid-cols-4 gap-6">
          <Stat title="Total Karyawan" value="247" icon={Users} />
          <Stat title="Survey Aktif" value="Semester 2 2024" icon={Calendar} />
          <Stat title="Completion Rate" value="73%" icon={ClipboardCheck} />
          <Stat title="Avg NPS Score" value="52%" icon={TrendingUp} />
        </div>

        <div className="p-6 border border-gray-200 card bg-base-100 rounded-xl">
          <h3 className="mb-4 text-lg font-bold text-gray-900">Quick Actions</h3>
          <div className="grid grid-cols-4 gap-4">
            <button onClick={() => setCurrentPage && setCurrentPage('employees')} className="p-6 border-2 border-dashed rounded-lg hover:bg-emerald-50">Add Employee</button>
            <button onClick={() => setCurrentPage && setCurrentPage('parameters')} className="p-6 border-2 border-dashed rounded-lg hover:bg-emerald-50">Manage Parameters</button>
            <button onClick={() => setCurrentPage && setCurrentPage('reports')} className="p-6 border-2 border-dashed rounded-lg hover:bg-emerald-50">View Reports</button>
            <button onClick={() => setCurrentPage && setCurrentPage('periods')} className="p-6 border-2 border-dashed rounded-lg hover:bg-emerald-50">Survey Periods</button>
          </div>
        </div>
      </div>
    </div>
  );
}
